.hero-section {
    display: flex;
    background-image: linear-gradient(to right, #0a6684, #5b8360);
    padding: 70px;
    color: white;
    word-spacing: 1px;
    font-size: 18px;
  }
  
  .hero-content {
    flex: 1;
    padding-right: 50px;
    opacity: 0;
    transform: translateY(40px);
    transition: opacity 2.0s ease, transform 2.0s ease;
    color: white;
  }
  
  .hero-content.reveal {
    opacity: 1;
    transform: translateY(0);
  }
  
  .hero-content h2 {
    font-size: 2.2em;
    margin-top: 0;
  }
  
  .features {
    list-style-type: none;
    padding: 0;
  }
  
  .features li {
    margin: 10px 0;
    color: #ffffff;
  }
  
  .form-section {
    background-color: white;
    padding: 30px;
    border-top-left-radius: 40px;
    border-bottom-right-radius: 40px;
    width: 500px;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
    opacity: 0;
    transform: translateY(40px);
    transition: opacity 2.0s ease, transform 2.0s ease;
  }
  
  .form-section.reveal {
    opacity: 1;
    transform: translateY(0);
  }
  
  form {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .radio-group {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 10px;
  }
  
  .radio-group label {
    display: flex;
    align-items: center;
    gap: 5px;
    color: black;
  }
  
  .input-group {
    display: flex;
    gap: 20px;
  }
  
  input[type="text"], 
  input[type="email"], 
  input[type="tel"], 
  textarea {
    width: 100%;
    padding: 10px;
    border: none;
    border-bottom: 1px solid #ccc;
    font-size: 16px;
  }
  
  textarea {
    height: 30px;
    resize: vertical;
  }
  
  .file-upload {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 20px;
  }
  
  .file-upload label {
    color: #666;
    font-size: 14px;
  }
  
  .file-input-wrapper {
    display: flex;
    align-items: center;
    gap: 10px;
    flex-wrap: wrap;
  }
  
  .file-upload input[type="file"] {
    display: none;
  }
  
  .file-upload-label {
    background-color: #f0f0f0;
    padding: 8px 15px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    color: #333;
    border: 1px solid #ccc;
  }
  
  .file-name {
    color: #666;
    font-size: 14px;
  }
  
  button[type="submit"] {
    background-color: black;
    color: white;
    border: none;
    padding: 12px 25px;
    border-radius: 25px;
    cursor: pointer;
    font-weight: bold;
    align-self: flex-end;
    font-size: 16px;
  }
  
  /* Mobile Responsiveness */
  @media (max-width: 768px) {
    .hero-section {
      flex-direction: column;
      padding: 30px;
    }
  
    .hero-content {
      padding-right: 0;
      margin-bottom: 30px;
    }
  
    .form-section {
      width: 90%;
      padding: 20px;
      border-radius: 20px;
    }
  
    .input-group {
      flex-direction: column;
      gap: 10px;
    }
  
    button[type="submit"] {
      align-self: center;
      width: 100%;
    }
  }
  
  /* Additional responsiveness for smaller screens */
  @media (max-width: 480px) {
    .hero-content h2 {
      font-size: 1.8em;
    }
  
    .form-section {
      padding: 15px;
    }
  
    .radio-group {
      flex-direction: column;
      align-items: center;
    }
  }
  